import { defineComponent } from 'vue';
export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    tableData: {
      type: Array,
      required: true
    },
    propList: {
      type: Array,
      required: false
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    listCount: {
      type: Number,
      default: 0
    },
    page: {
      type: Object,
      default: () => ({
        currentPage: 0,
        pageSize: 10
      })
    },
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:page'],
  setup(props, {
    emit
  }) {
    const handleSelectionChange = value => {
      //需要把数据传到对应组件
      emit('selectionChange', value);
    };
    const handleSizeChange = pageSize => {
      emit('update:page', {
        ...props.page,
        pageSize
      });
    };
    const handleCurrentChange = currentPage => {
      emit('update:page', {
        ...props.page,
        currentPage
      });
    };
    return {
      handleSelectionChange,
      handleSizeChange,
      handleCurrentChange
    };
  }
});